<template>
  <div>
    <mu-appbar style="width: 100%;" color="#4ed995" class="header">
      <a @click="()=>{
          $router.go(-1)
          }">
        <mu-button icon slot="left" class="icon" size="20">
          <mu-icon value="keyboard_arrow_left"></mu-icon>
        </mu-button>
      </a>
      <span>点位信息</span>
    </mu-appbar>

    <div class="Content">
      <div class="Title">
        <span>编辑当前点位信息</span>
      </div>
      <div>
        <div class="containerMain">
          <div class="flexContainer">
            <div class="titleselect">
              <span>点位名称：</span>
            </div>
            <div class="select" id="classPicstation" @click="selectDataPicStation">
              <input type="text" placeholder="请输入当前的站点名称" class="input" disabled v-model="station" />
            </div>
          </div>
          <div class="flexContainer">
            <div class="titleselect">
              <span>时间：</span>
            </div>
            <div class="select">
              <input
                type="text"
                class="input"
                style="background:white"
                disabled
                placeholder="时间为当前系统提交时间"
                v-model="dateTime"
              />
            </div>
          </div>
        </div>
        <div class="containerMain">
          <div class="flexContainer">
            <div class="titleselect">
              <span>地址：</span>
            </div>
            <div class="select" @click="getposition">
              <input
                type="text"
                class="input"
                style="background:white"
                disabled
                placeholder="点击获取当前地址"
                v-model="address"
              />
            </div>
          </div>
          <div class="flexContainer">
            <div class="titleselect">
              <span>经度：</span>
            </div>
            <div class="select">
              <input type="text" placeholder="当前经度" class="input" disabled v-model="lng" />
            </div>
          </div>
          <div class="flexContainer">
            <div class="titleselect">
              <span>纬度：</span>
            </div>
            <div class="select">
              <input
                type="text"
                class="input"
                style="background:white"
                disabled
                placeholder="当前纬度"
                v-model="lat"
              />
            </div>
          </div>
        </div>
        <div class="Title">
          <span>确认最新的数据</span>
        </div>
        <div class="containerMain" v-for="(item,index) in itemRecentData" :key="index">
          <div class="flexContainer" @click="clickDataBottom(index,item.id)">
            <div>时间：</div>
            <div class="flex">
              <div class="imageSize" v-show="item.imgShow">
                <img src="static/img/15.png" alt />
              </div>
              <div class="imageSize">
                <img src="static/img/right.png" alt />
              </div>
            </div>
          </div>
        </div>
        <div class="Title">
          <span>上传点位图片</span>
        </div>
        <div class="backPic">
          <div @click="()=>{showCamera=true}">
            <img src="static/img/110.png" alt />
          </div>
        </div>
        <div class="submit" @click="submit">
          <span>提交点位信息</span>
        </div>
      </div>
    </div>
    <mu-container>
      <mu-bottom-sheet :open.sync="open">
        <mu-list
          @item-click="closeBottomSheet"
          v-for="(item,index) in itemListContent"
          :key="index"
        >
          <mu-sub-header>时间：{{item.gmtCreate}}</mu-sub-header>
          <mu-list-item class="bottomBorder">
            <div class="flexContent">
              <div class="titleselect">pH</div>
              <div class="select">
                <input type="text" disabled :value="item.ph" class="input right" />
              </div>
            </div>
          </mu-list-item>
          <mu-list-item class="bottomBorder">
            <div class="flexContent">
              <div class="titleselect">总铜</div>
              <div class="select">
                <input type="text" disabled :value="item.cuprum+'  mg/L'" class="input right" />
              </div>
            </div>
          </mu-list-item>
          <mu-list-item class="bottomBorder">
            <div class="flexContent">
              <div class="titleselect">总氮</div>
              <div class="select">
                <input type="text" disabled :value="item.nitrogen+'  mg/L'" class="input right" />
              </div>
            </div>
          </mu-list-item>
          <mu-list-item class="bottomBorder">
            <div class="flexContent">
              <div class="titleselect">总磷</div>
              <div class="select">
                <input type="text" disabled :value="item.phosphorus+'  mg/L'" class="input right" />
              </div>
            </div>
          </mu-list-item>
          <mu-list-item class="bottomBorder">
            <div class="flexContent">
              <div class="titleselect">总铬</div>
              <div class="select">
                <input type="text" disabled :value="item.chromium+'  mg/L'" class="input right" />
              </div>
            </div>
          </mu-list-item>
          <mu-list-item class="bottomBorder">
            <div class="flexContent">
              <div class="titleselect">总镍</div>
              <div class="select">
                <input type="text" disabled :value="item.nickel+'  mg/L'" class="input right" />
              </div>
            </div>
          </mu-list-item>
          <mu-list-item class="bottomBorder">
            <div class="flexContent">
              <div class="titleselect">总氰化物</div>
              <div class="select">
                <input type="text" disabled :value="item.prussiate+'  mg/L'" class="input right" />
              </div>
            </div>
          </mu-list-item>
          <mu-list-item class="bottomBorder">
            <div class="flexContent">
              <div class="titleselect">氨氮</div>
              <div class="select">
                <input
                  type="text"
                  disabled
                  :value="item.ammoniaNitrogen+'  mg/L'"
                  class="input right"
                />
              </div>
            </div>
          </mu-list-item>
          <mu-list-item class="bottomBorder">
            <div class="flexContent">
              <div class="titleselect">污染指数</div>
              <div class="select">
                <input
                  type="text"
                  disabled
                  :value="item.pollutionIndex+'  mg/L'"
                  class="input right"
                />
              </div>
            </div>
          </mu-list-item>
          <mu-list-item>
            <div class="btn" @click="btnSelect(index,item.id)">
              <span>确认当前数据</span>
            </div>
          </mu-list-item>
        </mu-list>
      </mu-bottom-sheet>
    </mu-container>

    <van-dialog v-model="show" title="请手动输入污染源名称" show-cancel-button>
      <div class="select centerBorder" style="height:60px;">
        <input
          type="text"
          class="input"
          placeholder="请输入您的污染源名称"
          v-model="station"
          style="width:80%"
        />
      </div>
    </van-dialog>

    <van-action-sheet
      v-model="showCamera"
      :actions="actions"
      cancel-text="取消"
      @cancel="onCancel"
      @select="selectBtn"
    />
  </div>
</template>
<script>
import AppBace from "../../../static/ajax";
import CameraPointPic from "../../../static/js/cordovaCamare/camera";
import { Toast } from "vant";
import Vue from "vue";
import { Dialog } from "vant";
Vue.use(Dialog);
import { Notify } from "vant";
export default {
  data() {
    return {
      station: "",
      dateTime: AppBace.filterTime(new Date()),
      arr: [],
      itemListContent: [],
      open: false,
      itemRecentData: [],
      select: 0,
      colorB: "",
      address: "",
      lng: "",
      lat: "",
      token: "",
      carId: 0,
      show: false,
      openFullscreen: false,
      result: [],
      list: [],
      showCamera: false,
      actions: [{ name: "拍照" }, { name: "相册" }]
    };
  },
  async created() {
    // 数据冻结
    // const users = await axios.get("/api/users");
    // this.users = Object.freeze(users);
    this.token = sessionStorage.getItem("token");
    this.arr = this.$store.state.enterpriseNum;
    // this.arr.push("手动输入企业名称或者污染源站点名称");
    this.list = this.$store.state.menu;
    await this.initData();
  },
  mounted() {},
  methods: {
    selectBtn(item, index) {
      console.log(item.name);
      this.showCamera = false;
      if (this.carId !== 0) {
        CameraPointPic.cameraTakePoint(index, this.carId);
      } else {
        Toast("请选择当前最新的数据，再上传图片");
      }
    },
    onCancel() {
      this.showCamera = false;
      Toast("cancel");
    },
    toggle(index) {
      this.$refs.checkboxes[index].toggle();
    },
    async initData() {
      var url = "/mobile/point/data";
      var vehicleId = this.$store.state.vehicleIdStatus;
      var res = await AppBace.request(
        url,
        "GET",
        {
          vehicleId
        },
        this.token
      );
      console.log(res);
      this.itemListContent = Object.freeze(res.data.data);
      this.itemRecentData = Object.freeze(res.data.data);
      // this.$http.get(url,{
      //   vehicleId:this.$store.state.vehicleIdStatus
      // }).then((res)=>{
      //   console.log(res);
      // }).catch((err)=>{
      //   console.log(err);
      // })
    },
    selectDataPicStation() {
      // this.open = false;
      var _this = this;
      var pickerView = new PickerView({
        bindElem: classPicstation, // 绑定的元素,用于区别多个组件存在时回显区别，如果单个可以随意填某个元素
        data: this.arr, // 说明：该参数必须符合json格式 且最里层是个数组，如上面的data变量所展示的[3,4]。
        title: "选择点位", // 顶部标题文本 默认为“标题”
        leftText: "取消", // 头部左侧按钮文本 默认为‘取消’
        rightText: "确定", // 头部右侧按钮文本 默认为“确定”
        rightFn: function(selectArr) {
          // 点击头部右侧按钮的回调函数，参数为一个数组，数组对应滚轮中每项对应的值
          console.log(selectArr[0]);
          switch (selectArr[0]) {
            case "其他":
              _this.show = true;
              break;

            default:
              _this.station = selectArr[0];
              break;
          }
        }
      });
    },
    async submit() {
      if (this.carId === 0) {
        Toast("请选择当前最新数据！");
      } else {
        this.dataTime = await AppBace.filterTime(new Date());
        console.log(this.dataTime);
        var dataId = this.carId;
        var pollutantName = this.station;
        var routingName = this.$store.state.routingName;
        var url = "/mobile/point";
        var arr = [];
        var array = [];
        await this.$http
          .post(url, {
            pollutantName: pollutantName,
            routingName: routingName,
            dataId: dataId
          })
          .then(res => {
            console.log("dian", res);
            if (res.data.message === "success") {
              if (
                this.$route.query.name !== "" &&
                this.$route.query.name !== undefined
              ) {
                console.log("store", this.$store.state.pointName);
                array = this.$store.state.pointName;
                for (let index = 0; index < array.length; index++) {
                  var id = array[index].id;
                  if (this.$route.query.id === id) {
                    array[index] = {
                      time: AppBace.filterTime(new Date()),
                      pollutantName: pollutantName,
                      routingName: routingName,
                      dataId: dataId,
                      id: this.$store.state.id++
                    };
                  }
                }
                this.$store.commit("pointDataList", array);
                Toast("点位信息已经重新修改");

                Notify({
                  type: "warning",
                  message: "车辆准备出库，请谨慎驾驶车辆，遵守交规行驶，谢谢！",
                  duration: 5000
                });
              } else {
                arr = this.$store.state.pointName;
                arr.push({
                  time: AppBace.filterTime(new Date()),
                  pollutantName: pollutantName,
                  routingName: routingName,
                  dataId: dataId,
                  id: this.$store.state.id++
                });
                Toast("新增点位成功");
                this.$store.commit("pointDataList", arr);
              }
              this.$router.go(-1);
            }
          })
          .catch(err => {
            console.log(err);
          });
      }
    },
    closeBottomSheet() {
      this.open = false;
    },
    clickDataBottom(indexI, id) {
      this.open = true;
      this.select = indexI;
      var arr = [];
      arr = this.itemListContent;
      for (let index = 0; index < arr.length; index++) {
        var arrId = arr[index].id;
        if (id === arrId) {
          this.itemListContent = [];
          this.itemListContent.push(arr[index]);
          this.carId = id;
        }
      }
    },
    btnSelect(index, id) {
      console.log(this.select);
      this.open = false;
      for (let index = 0; index < this.itemRecentData.length; index++) {
        this.itemRecentData[index].imgShow = false;
      }
      this.itemRecentData[this.select].imgShow = true;
    },
    getposition(val) {
      var _this = this;

      navigator.vibrate(500);
      var me = {
        msg: ""
      };
      var onSuccess = function(position) {
        console.log("position", position);
        var lat = "";
        var lng = "";
        var wgs84togcj02 = coordtransform.wgs84togcj02(
          Number(position.coords.longitude),
          Number(position.coords.latitude)
        );
        console.log(wgs84togcj02);
        _this.lng = wgs84togcj02[0];
        _this.lat = wgs84togcj02[1];
        _this.getGaoDE(wgs84togcj02[0], wgs84togcj02[1]);
        //   return arr;
      };
      var error = function(error) {
        me.msg =
          "code: " + error.code + "\n" + "message: " + error.message + "\n";

        alert(me.msg);
        console.log(me.msg);
      };

      navigator.geolocation.getCurrentPosition(onSuccess, error, {
        maximumAge: 30000,
        timeout: 30000,
        enableHighAccuracy: true
      });
    },
    getGaoDE(e, f) {
      var geocoder;
      var that = this;
      var lnglatXY = new AMap.LngLat(Number(e), Number(f));
      //加载地理编码插件
      AMap.plugin(["AMap.Geocoder"], function() {
        geocoder = new AMap.Geocoder({
          radius: 1000, //以已知坐标为中心点，radius为半径，返回范围内兴趣点和道路信息
          extensions: "all" //返回地址描述以及附近兴趣点和道路信息，默认"base"
        });
        //返回地理编码结果
        AMap.event.addListener(geocoder, "complete", geocoder_callBack);
        //逆地理编码
        geocoder.getAddress(lnglatXY);
      });
      function geocoder_callBack(params) {
        console.log("params", params);
        that.address = params.regeocode.formattedAddress;
        // alert("当前" + params.regeocode.formattedAddress);
        // alert("状态" + params.info);
        // alert(params.type);
        // alert("数组" + params.regeocode, addressComponent);
      }
    }
  }
};
</script>
<style scoped>
@import "../../../static/css/maintain.css";
@import "../../../static/css/model.css";
.Content {
  padding: 0 5px;
}
.submit {
  background-color: #4ed995;
  color: white;
  height: 40px;
  border-radius: 10px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 16px;
  margin-top: 20px;
  box-shadow: 5px 5px 5px #ccc;
}
.submit:active:focus,
.submit:active:hover {
  /* background-color:lightseagreen; */
  color: rgb(177, 253, 253);
  background-color: rgb(59, 248, 169);
  border-color: #8c8c8c;
}
.Title {
  background-color: white;
  height: 30px;
  border-radius: 5px;
  display: flex;
  align-items: center;
  padding: 5px;
  margin-top: 20px;
  font-size: 14px;
  font-weight: 600;
}
.imageSize img {
  width: 12px;
  height: 12px;
}
.flexContent {
  display: flex;
  justify-content: space-between;
  width: 100%;
}
.bottomBorder {
  border-bottom: 1px rgb(233, 233, 233) solid;
}
.right {
  text-align: right;
  /* border: none; */
}
.btn {
  background-color: #4ed995;
  color: white;
  width: 100%;
  height: 30px;
  border-radius: 5px;
  display: flex;
  align-items: center;
  justify-content: center;
}
.flex {
  display: flex;
  flex-direction: row;
  width: 60px;
  justify-content: space-evenly;
}
.centerBorder {
  display: flex;
  justify-content: center;
  align-items: center;
}
.size {
  font-size: 14px;
}
.marginTop {
  margin-top: 5px;
}
.checkBox {
  padding: 10px;
}
.backPic {
  background: white;
}
</style>


